import React, { Component } from 'react'
import Layout from '../components/layout'; 
import Head from 'next/head';

export default class editDevice extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: {}
        }
    }

    static getInitialProps ({ query: { id } }) {
      return { id: id }
    }

    componentDidMount() {
        console.log(this.props.id)
        fetch(`/api/getDevice/${this.props.id}`)
          .then(res => res.json())
          .then(user => this.setState({data: user.resp[0]}));
      }

    render() {
            return (
            <div>
                <Head>
                    <title>Devices</title>
                    <meta name="viewport" content="initial-scale=1.0, width=device-width" />
                    <link rel="stylesheet" href="//cdn.jsdelivr.net/npm/semantic-ui@2.4.1/dist/semantic.min.css"/>
                </Head>
                <Layout>
                    <Form>
                        <Form.Group>
                            <Form.Input label='First name' placeholder='First Name' width={6} />
                            <Form.Input label='Middle Name' placeholder='Middle Name' width={4} />
                            <Form.Input label='Last Name' placeholder='Last Name' width={6} />
                        </Form.Group>
                        <Form.Group>
                            <Form.Input placeholder='2 Wide' width={2} />
                            <Form.Input placeholder='12 Wide' width={12} />
                            <Form.Input placeholder='2 Wide' width={2} />
                        </Form.Group>
                        <Form.Group>
                            <Form.Input placeholder='8 Wide' width={8} />
                            <Form.Input placeholder='6 Wide' width={6} />
                            <Form.Input placeholder='2 Wide' width={2} />
                        </Form.Group>
                    </Form>
                </Layout>
            </div>
        )
}
}